    border: none;
    border-bottom: 1px solid var(--border-color);
}

/* CHANGE PASSWORD */

.change-password-title-container {
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.change-password-form {
    width: 100%;
    height: 60%;
    padding-top: 20px;
}

.change-password-form button {
    width: 60%;
}

/* DELETE ACCOUNT */

.delete-account-title-container {
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.delete-account-confirmation {
    width: 100%;
    height: 40%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: large;
    color: #444;
}

.delete-account-confirmation button {
    margin-top: 20px;
}
.sidebar {
    position: fixed;
    width: 22.4%;
    height: 100%;
    background: #fff;
    padding: 30px 0;
    border-right: 1px solid var(--border-color);
}

.sidebar ul li {
    color: #000;
    font-size: 1.2rem;
    font-weight: bold;
    display: block;
    margin: 0 30px;
    padding: 15px;
    border-radius: 20px;
}

.sidebar ul li .sidebar-menu-icon {
    font-size: 1.25rem!important;
    margin-right: 10px;
}

.sidebar .logo-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
}

.sidebar ul li .fas {
    width: 25px;
}

.sidebar h2:hover {
    cursor: pointer;
}

.sidebar ul li:hover {
    background: var(--secondary-color);
    color: var(--primary-color);
    cursor: pointer;
}

:root {
  --primary-color: #1DA1F2;
  --secondary-color: #e8f5fe;
  --primary-color-hover: #0b80d0;
  --white-hover: #eee;
  --border-color: #E6ECF0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

html {
  font-size: 16px;
}

body {
  background: #fff;
}

btn, input {
  overflow: hidden;
}

.wrapper {
  display: flex;
}

.nav-menu-item-active {
  color: #1DA1F2!important;
  color: var(--primary-color)!important;
}

.notification-text-container {
  display: inline-block;
  position: relative;
  bottom: 10px;
  min-width: 15px;
  height: 18px;
  border-radius: 50%;
}

.notification-text-container .notification-text {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff!important;
  background-color: #f00!important;
  font-size: 0.75rem!important;
  border-radius: 50%;
  padding: 2px;
}

.wrapper .main-content {
  width: 100%;
  height: 100%;
  margin-left: 22.4%;
}

.wrapper .main-content .header {
  height: 12vh;
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  align-items: center;
  top: 0;
  padding: 20px 16% 20px 20px;
  background: #fff;
  color: #000;
  font-size: 1.2rem;
  border-bottom: 1px solid #E6ECF0;
  border-bottom: 1px solid var(--border-color);
}

.wrapper .main-content .header .logout {
  color: #1DA1F2;
  color: var(--primary-color);
  cursor: pointer;
}

.wrapper .main-content .content {
  position: relative;
  text-align: center;
}

.home-page-subheader {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 0 5px 0;
  border-bottom: 1px solid #E6ECF0;
  border-bottom: 1px solid var(--border-color);
  background: #fff;
  font-size: 1.2rem;
  color: #333;
}

.loader {
  width: 28px;
  height: 28px;
  background: rgba(0, 0, 0, 0);
  border: 4px solid #D2ECFC;
  border-top: 4px solid #1DA1F2;
  border-top: 4px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 0.7s linear infinite;
}

/* BUTTONS */

.btn-primary {
  background: #1DA1F2!important;
  background: var(--primary-color)!important;
  color: #fff!important;
  border: 1px solid rgba(0, 0, 0, 0.1)!important;
  border-radius: 20px;
  font-weight: bold!important;
}

.btn-primary:hover {
  background-color: #0b80d0!important;
  background-color: var(--primary-color-hover)!important;
  color: #fff!important;
}

.btn-secondary {
  background: rgba(0, 0, 0, 0)!important;
  color: #1DA1F2!important;
  color: var(--primary-color)!important;
  border: 1px solid #1DA1F2!important;
  border: 1px solid var(--primary-color)!important;
  border-radius: 20px!important;
  font-weight: bold!important;
}

.btn-secondary:hover {
  background: #e8f5fe!important;
  background: var(--secondary-color)!important;
}

.left-arrow-icon, .add-icon {
  padding: 10px;
  margin-right: 10px;
  color: #1DA1F2;
  color: var(--primary-color);
  border-radius: 50%;
  font-size: 1.25rem;
}

.left-arrow-icon:hover, .add-icon:hover {
  background-color: #e8f5fe;
  background-color: var(--secondary-color);
  color: #0b80d0;
  color: var(--primary-color-hover);
  cursor: pointer;
}

.trash-icon:hover {
  color: #f00!important;
  cursor: pointer;
}

.btn-grey {
  background: #eee;
  color: #444;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  font-weight: bold;
}

.btn-grey:hover {
  background: #eee;
  background: var(--white-hover);
}

.btn-primary:focus, .btn-secondary:focus, .btn-grey:focus {
  outline: none;
  box-shadow: none;
}

/* PROFILE-IMAGE */

.profile-img-sm {
  border-radius: 50%;
  width: 48px;
  height: 48px;
}

.profile-img-med {
  border-radius: 50%;
  width: 64px;
  height: 64px;
}

.profile-img-big {
  border-radius: 50%;
  width: 96px;
  height: 96px;
}

.profile-img-sm:hover, .profile-img-med:hover {
  transform: scale(1.01);
}

/* GENERAL UTILS */

.emoji-list-container {
  display: none;
  position: absolute;
  left: 20px;
  z-index: 1;
  border: 2px solid rgba(20, 20, 20, 0.3);
}

.view-more-select {
  position: absolute;
  z-index: 1;
  display: none;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  text-align: start;
  background: #fff;
}

.view-more-select li {
  padding: 15px;
}

.view-more-select li:hover {
  background: #eee;
  background: var(--white-hover);
  cursor: pointer;
}
@media (max-width: 1165px) {
    .sidebar ul li {
        margin: 0;
    }
}

@media (max-width: 980px) {
    .wrapper .sidebar {
        display: none;
    }
    .wrapper .main-content .content .create-post-form {
        display: none;
    }
    .wrapper .main-content .bottom-menu, .bottom-menu-action-icon {
        display: inline;
        display: initial;
    }
    .wrapper .main-content {
        margin: 0;
    }
    .fa-smile {
        display: none;
    }
    .post-list, .profile-list-container, .comment-list, .interest-list-container, .interest-profile-list,
    .interests-page-container, .settings-page-container, .settings-description-container, .notifications-container, .chats-container {
        padding-bottom: 10vh;
    }
    .list-group-item {
        padding: 1rem 3rem;
    }
    .bottom-menu ul li:hover {
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
        background: initial;
    }
    .profile-list-container .profile-row:hover {
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
        background: initial;
    }
    .post-container:hover {
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
        background: initial;
    }
    .post-actions .fa-comment:hover, .post-actions .fa-heart:hover {
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
        background: initial;
    }
    .post-actions .far:hover {
        color: #6c757d;
    }
    .profile-page-menu .profile-page-menu-item:hover {
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
        background: initial;
    }
    .left-arrow-icon:hover, .add-icon:hover, .btn-secondary:hover {
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll!important;
